import React from "react";
import { ScrollRestoration } from "react-router-dom";
import { useContext, useEffect } from "react";

import MainContext from "../context/main-context";
import PricingPlans from "../components/sections/pricing-plans/PricingPlans";
import GetStarted from "../components/modals/get-started/GetStarted";
import PricingHead from "../components/layout/pricing-head/PricingHead";
import QuoteBlock from "../components/sections/quote-block/QuoteBlock";
import Footer from "../components/footer/Footer";
import PricingForm from "../components/sections/pricing-form/PricingForm";
import PageWraper from "../components/layout/page-wraper/PageWraper";

function PricingPage() {
  const Ctx = useContext(MainContext);

  useEffect(() => {
    const timer = setTimeout(() => {
      if (Ctx.modal === false) {
        Ctx.showModal();
      }
    }, "3000");
    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <>
      <PageWraper>
        <PricingHead />
        <QuoteBlock />
        <PricingPlans />
        <PricingForm />
        <Footer />
        {Ctx.modal && <GetStarted />}
        <ScrollRestoration />
      </PageWraper>
    </>
  );
}

export default PricingPage;
