import classes from "./about-us.module.scss";
import MainTitle from "../../layout/main-title/MainTitle";

function AboutUs() {
  return (
    <>
      <div id="about-us" className={classes["about-us"]}>
        <div className={classes.top}>
          <MainTitle
            text="Who we are?"
            slogan="We are business savier!"
            aos="fade-down"
          />
          <div className={classes.between} data-aos="slide-right">
            <img src="/images/5124556.png" alt="Call Center Guy" />
          </div>
          <p className={classes.description} data-aos="fade-up">
            Our team of professionals is ready to help you get the market
            exposure you need. We understand that every journey is different, so
            we take the time to understand your unique strategies and needs. We
            also know that there may be some hurdles along the way, but we're
            here to guide you through them and take your lead generation to the
            next level!
          </p>
        </div>
        <div className={classes.bottom} data-aos="slide-right">
          <img src="/images/5124556.png" alt="Call Center Guy" />
        </div>
      </div>
    </>
  );
}

export default AboutUs;
