import MainTitle from "../../layout/main-title/MainTitle";
import { HashLink } from "react-router-hash-link";
import classes from "./pricing-plans.module.scss";
import { useState, useEffect } from "react";

function PricingPlans() {
  const [plans, setPlans] = useState([]);

  useEffect(() => {
    fetch("/api/plans")
      .then((res) => res.json())
      .then((plans) => setPlans(plans.data));
  }, []);

  function clickCounter(name) {
    fetch(`/api/${name.toLowerCase()}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        console.log(data);
      });
  }

  return (
    <>
      <div className={classes["pricing-plans"]} id="pricing-plans">
        <MainTitle
          text="Pricing Plans"
          slogan="We Offer You Planets."
          aos="fade-down"
        />
        <div className={classes.plans}>
          {plans &&
            plans.map((plan) => {
              return (
                <div
                  className={classes.plan}
                  data-aos="fade-up"
                  data-aos-once="true"
                  key={plan.id}
                >
                  <div
                    className={classes.top}
                    data-aos="zoom-in"
                    data-aos-once="true"
                  >
                    <div
                      className={`${classes.icon}`}
                      style={{
                        backgroundImage: `url(/images/${plan.name}.jpg)`,
                      }}
                    >
                      <img src={`/images/${plan.name}.png`} alt="Earth Icon" />
                    </div>
                  </div>
                  <div className={classes.bottom}>
                    <div
                      className={classes.title}
                      data-aos="fade-up"
                      data-aos-once="true"
                    >
                      <h3>{plan.name}</h3>
                      <p>{plan.title}</p>
                    </div>
                    <div className={classes.features}>
                      <h4 data-aos="fade-right" data-aos-once="true">
                        Services:
                      </h4>
                      <ul>
                        {plan.service1 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service1}
                          </li>
                        )}
                        {plan.service2 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service2}
                          </li>
                        )}
                        {plan.service3 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service3}
                          </li>
                        )}
                        {plan.service4 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service4}
                          </li>
                        )}
                        {plan.service5 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service5}
                          </li>
                        )}
                        {plan.service6 && (
                          <li data-aos="fade-right" data-aos-once="true">
                            {plan.service6}
                          </li>
                        )}
                      </ul>
                    </div>
                    <div
                      className={classes.price}
                      data-aos="fade-up"
                      data-aos-once="true"
                    >
                      <div className={classes.prices}>
                        <h5>{`$${plan.price} USD`}</h5>
                        <p>${`${plan.discount}`} USD</p>
                      </div>
                      <p>{plan.priceDesc}</p>
                    </div>
                    <div
                      className={classes.cta}
                      data-aos="fade-up"
                      data-aos-offset="0"
                      data-aos-once="true"
                    >
                      <HashLink
                        to="/pricing-plans#contact-form"
                        onClick={() => clickCounter(plan.name)}
                      >
                        Get a Quote
                      </HashLink>
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    </>
  );
}

export default PricingPlans;
